@tailwind base;
@tailwind components;
@tailwind utilities;        


@layer components{
.btn{
    @apply  focus:ring focus:ring-opacity-50   focus:ring-offset-2 text-white px-4 py-2 inline-block rounded-lg uppercase tracking-wide font-bold text-sm;
}
.btn-primary{
    @apply  active:bg-indigo-800   hover:bg-indigo-400   focus:ring-indigo-400  bg-indigo-500 ;
}
.btn-secondary{
    @apply  active:bg-gray-800   hover:bg-gray-400   focus:ring-gray-400  bg-gray-500 ;
}
}

